import React, { useState } from "react";
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import AddIcon from '@mui/icons-material/Add';
const Componrnet =()=>{
    const [searchVal,setSerchVal]=useState("");
    const [submitVal,setSubmit]=useState([])
    const serchValue=(event)=>{
        setSerchVal(event.target.value);
    }
    const submiValue=()=>{
        if(!searchVal){

        }else{
            setSubmit([...submitVal,searchVal]);
            submitVal(" ");
        }
            
    }
    // delete one by one
    const delitem=(id)=>{
            const upadate=submitVal.filter((value, index)=>{
                return index != id
            })
            setSubmit(upadate)
    }
    const removeALl=()=>{
        setSubmit([]);
    }
    return(
        <>
        <section className="main">

        
          <div className="container">
                <div className="row justify-content-center pt-5 align-items-center">
                <div className="col-md-4">
                       <div className="box">
                       <h1>Medical To Do List</h1>
                            <input type="search" name="" id="" onChange={serchValue} /><button className="add" onClick={submiValue}><AddIcon/></button>
                          
                       {
                        submitVal.map((value, index)=>{
                            return(
                               
                                <div className="d-flex m-4 justify-content-space align-items-center productView flex-xl-row" key={index}>
                                <div className="text-left">{value}</div>
                                <div><button className="btnmain " onClick={()=>delitem(index)}> <DeleteForeverIcon/></button></div>
                            </div>
                           
                        )
                        })
                       }
                      
                       <button type="submit" data-sm-link-text="Remove All" onClick={removeALl} className="btn btn-success">Remove All</button>
                       </div>
                </div>
                </div>
          </div>   
          </section>
        </>
    )
    };
export default Componrnet;
